@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,300;0,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Source Sans Pro', Arial, Helvetica, sans-serif;
  font-size: 3.5rem;
  font-weight: 700;
  color: #dfe6e9;
}
.calculator {
  width: 60rem;
  height: 60rem;
  padding: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #2d3436;
  border: 7px solid;
  border-top-color: rgba(255, 255, 255, 0.7);
  border-left-color: rgba(255, 255, 255, 0.5);
  border-bottom-color: rgba(255, 255, 255, 0.2);
  border-right-color: rgba(255, 255, 255, 0.3);
  border-radius: 5px;
  box-shadow: 1px 3px 25px rgba(0, 0, 0, 0.5);
}
.calculator__buttons {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 1rem;
}
.calculator__button {
  display: grid;
  justify-content: center;
  align-content: center;
  color: #7a8387;
  text-shadow: 1px 1px rgba(255, 255, 255, 0.5), -1px -1px rgba(0, 0, 0, 0.5);
  background-color: #636e72;
  border: 5px solid;
  border-top-color: rgba(255, 255, 255, 0.7);
  border-left-color: rgba(255, 255, 255, 0.5);
  border-bottom-color: rgba(255, 255, 255, 0.1);
  border-right-color: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  transition: all 0.1s linear;
}

.calculator__button:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
  color: #dfe6e9;
}

.calculator__button.big {
  grid-column: 4;
  grid-row: 2 / 6;
  font-size: 7rem;
  color: #0984e3;
  text-shadow: 1px 1px rgb(134, 186, 225), -1px -1px rgb(6, 71, 121);
  background-color: #0984e3;
  transition: all 0.2s linear;
}

.calculator__button.big:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
  color: #dfe6e9;
}

.calculator__display {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 14rem;
  margin-bottom: 1.5rem;
  padding: 0 2.5rem;
  text-align: right;
  line-height: 1.2;
  background-color: #b2bec3;
  border: 5px solid;
  border-top-color: rgba(255, 255, 255, 0.9);
  border-left-color: rgba(255, 255, 255, 0.7);
  border-bottom-color: rgba(255, 255, 255, 0.3);
  border-right-color: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
  overflow-x: auto;
}

.calculator__expression {
  font-size: 2.2rem;
  color: #636e72;
}

.calculator__result {
  font-family: 'Orbitron', sans-serif;
  font-weight: 400;
  font-size: 5.5rem;
  line-height: 1;
  color: #2d3436;
}
